@use '../base/mixins' as m;

.galeria{
    &__heading{
        text-align: center;
        margin: 2rem 30rem;
        padding: 1rem;
        border-bottom: 1px solid hsla(0, 3%, 73%, .59);
        font-size: 2.2rem;
        text-transform: uppercase;
    }

    &__grid{
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 2rem;  
        margin: 1rem; 
        padding: 0 3rem;
    

        .imagen{
            overflow:hidden;
            
            img{
                height: 30rem;
                width: 30rem;
                object-fit: cover;
            }
    
            @include m.desktop { 
                &:nth-child(1){
                    grid-row: 1 / 3;
                    img{
                        height: 62rem; //cada imagen vale 30 entonces como va ocupar 2 lugares sera 30 + 30 = 60 y el 2 seria del gap del grid
                        object-position: center;
                    }
                }
    
                &:nth-child(2){
                    grid-column: 2 / 4;
                    img{
                        width: 62rem;
                    }
                }
    
                &:nth-child(4){
                    grid-row: 2 / 4;
                    img{
                        height: 62rem;
                        object-position: center;
                    }
                }
    
                &:nth-child(5){
                    grid-column: 3 / 5;
                    img{
                        width: 62rem;
                    }
                }
                &:nth-child(6){
                    grid-column: 3 / 5;
                    img{
                        width: 62rem;
                    }
                }
    
                &:nth-child(7){
                    grid-row: 3 / 5;
                    img{
                        height: 62rem;
                    }
                }
    
                &:nth-child(8){
                    grid-column: 2 / 5;
                    img{
                        width: 94rem;
                    }
                }
    
                &:nth-child(9){
                    grid-column: 1 / 3;
                    img{
                        width: 62rem;
                    }
                }
    
                &:nth-child(10){
                    grid-column: 3 / 5;
                    img{
                        width: 62rem;
                    }
                }
            }
        }

        .img:hover {-webkit-transform:scale(1.3);transform:scale(1.3);}
    }
}

